.skills{
    grid-column: body-start/body-end;
    // background-color: grey;

    display:grid;
    grid-template-columns: repeat(2, minmax(min-content, 1fr));

    &__heading{
        grid-column: 1/-1;
    }

    &__paragraph{
        grid-column: 1/-1;
    }

    &__list{    

        column-gap: 1rem;
        row-gap: 1rem;
        word-wrap: break-word;

        &--left{
            grid-column: 1/2;
            display:grid;
            grid-template-columns: max-content 1fr;
            padding-right: 1rem;
            // background-color: red;
            max-width: 100%;
           

            
        }
        &--right{
            grid-column: 2/3;
            display:grid;
            grid-template-columns: max-content 1fr;
            align-content: start;
            padding-left: 1rem;
            // background-color: blue;
            
        }

        &__header{
            grid-column: 1 /-1;

        }

        &__icon{
            //https://stackoverflow.com/questions/8977957/can-i-change-the-height-of-an-image-in-css-before-after-pseudo-elements
            height: 5rem;
            width: 5rem;
            grid-column: 1;
       
            align-self:start;

      
        }

        &__item{
            
            grid-column: 2;
            align-self: center;
            word-wrap: normal;
      
        }


    }
}